import { HttpClient } from '@angular/common/http';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatatableComponent } from '@swimlane/ngx-datatable';

@Component({
  selector: 'app-requestakg',
  templateUrl: './requestakg.component.html',
  styleUrls: ['./requestakg.component.sass']
})
export class RequestakgComponent implements OnInit {
  @ViewChild('roleTemplate', { static: true }) roleTemplate: TemplateRef<any>;
  @ViewChild(DatatableComponent, { static: false }) table: DatatableComponent;
  @ViewChild('closeAddModal', { static: false }) closeAddModal;
  @ViewChild('closeEditModal', { static: false }) closeEditModal;
  rows = [];
  selectedRowData: selectRowInterface;
  // newUserImg = 'assets/images/user/user1.jpg';
  data = [];
  filteredData = [];
  editForm: FormGroup;
  register: FormGroup;
  empdatass;
  selectedOption: string;
  columns = [
    { name: 'id' },
    { name: 'acknowledge' },
    { name: 'channel' },
    { name: 'date' },
    { name: 'empname' },
    { name: 'issuedby' },
    { name: 'itemcode' },
    { name: 'itemname' },
    { name: 'otherdescription' },
    { name: 'otp' },
    { name: 'quantity' },
    { name: 'remark' },
    { name: 'remarks' },
    { name: 'specdrawing' },
    { name: 'status' },
    { name: 'totalprice' },
  ];
  // Table 2
  tb2columns = [
    { name: 'id' },
    { name: 'acknowledge' },
    { name: 'channel' },
    { name: 'date' },
    { name: 'empname' },
    { name: 'issuedby' },
    { name: 'itemcode' },
    { name: 'itemname' },
    { name: 'otherdescription' },
    { name: 'otp' },
    { name: 'quantity' },
    { name: 'remark' },
    { name: 'remarks' },
    { name: 'specdrawing' },
    { name: 'status' },
    { name: 'totalprice' },

  ];
  tb2data = [];
  tb2filteredData = [];
  @ViewChild(DatatableComponent, { static: false }) table2: DatatableComponent;
  type: any;
  name: any;
  code: any;
  constructor(private fb: FormBuilder, private _snackBar: MatSnackBar , private http: HttpClient ) {
    this.editForm = this.fb.group({
      id: new FormControl(),
      acknowledge: new FormControl(),
      channel: new FormControl(),
      date: new FormControl(),
      empname: new FormControl(),
      issuedby: new FormControl(),
      itemcode: new FormControl(),
      itemname: new FormControl(),
      otherdescription: new FormControl(),
      otp: new FormControl(),
      quantity: new FormControl(),
      remark: new FormControl(),
      remarks: new FormControl(),
      specdrawing: new FormControl(),
      status: new FormControl(),
      totalprice: new FormControl(),
      rejectreason: new FormControl(),
      safestock: new FormControl(),
      rol: new FormControl(),
      livestock: new FormControl(),
      minstock: new FormControl(),

    });
  }
  mainurl
  ngOnInit() {
    this.mainurl = localStorage.getItem('mainurl');
    this.fetch(data => {
      this.data = data;
      this.filteredData = data;
    });
    // Table 2
    this.tb2fetch(data => {
      this.tb2data = data;
      this.tb2filteredData = data;
    });
    this.register = this.fb.group({
      //
      id: [''],
   acknowledge: [''],
   channel: [''],
   date: [''],
   empname: [''],
   issuedby: [''],
   itemcode: [''],
   itemname: [''],
   otherdescription: [''],
   otp: [''],
   quantity: [''],
   remark: [''],
   remarks: [''],
   specdrawing: [''],
   status: [''],
   totalprice: [''],
   rejectreason : [''],
   safestock: [''],
   rol: [''],
   livestock: [''],
   minstock: [''],

    });
  }
  fetch(cb) {

    const req = new XMLHttpRequest();
    req.open('GET', this.mainurl + 'empcomprequest');
    req.onload = () => {
      const data = JSON.parse(req.response);
      cb(data);
    };



    req.send();
  }
  // Table 2
  tb2fetch(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'assets/data/ngx-data.json');
    req.onload = () => {
      const data = JSON.parse(req.response);
      cb(data);
    };
    req.send();
  }
  // Table 2
  tb2filterDatatable(event) {
    // get the value of the key pressed and make it lowercase
    const val = event.target.value.toLowerCase();
    // get the amount of columns in the table
    const colsAmt = this.tb2columns.length;
    // get the key names of each column in the dataset
    const keys = Object.keys(this.tb2filteredData[0]);
    // assign filtered matches to the active datatable
    this.tb2data = this.tb2filteredData.filter(function(item) {
      // iterate through each row's column data
      for (let i = 0; i < colsAmt; i++) {
        // check for a match
        if (
          item[keys[i]]
            .toString()
            .toLowerCase()
            .indexOf(val) !== -1 ||
          !val
        ) {
          // found match, return true to add to result set
          return true;
        }
      }
    });
    // whenever the filter changes, always go back to the first page
    this.table2.offset = 0;
  }
  editRow(row, rowIndex) {
    this.editForm.setValue({
      id: row.id,
   acknowledge: row.acknowledge,
   channel: row.channel,
   date: row.date,
   empname: row.empname,
   issuedby: row.issuedby,
   itemcode: row.itemcode,
   itemname: row.itemname,
   otherdescription: row.otherdescription,
   otp: row.otp,
   quantity: row.quantity,
   remark: row.remark,
   remarks: row.remarks,
   specdrawing: row.specdrawing,
   status: row.status,
   totalprice: row.totalprice,
   rejectreason :row.rejectreason,
   safestock:row.safestock,
   rol: row.rol,
   livestock: row.livestock,
   minstock:row.minstock,

    });
    this.selectedRowData = row;
  }
  addRow() {
    this.register.patchValue({
      // id: this.getId(10, 100),
      name: this.name,
      code: this.code
    });
  }

  arrayRemove(array, id) {
    return array.filter(function(element) {
      return element.id != id;
    });
  }

  onEditSave(form: FormGroup) {

    this.data = this.data.filter((value, key) => {
      if (value.id == form.value.id) {
        value.id = form.value.id;
        value.acknowledge= form.value.acknowledge;
        value.channel= form.value.channel;
        value.date= form.value.date;
        value.empname= form.value.empname;
        value.issuedby= form.value.issuedby;
        value.itemcode= form.value.itemcode;
        value.itemname= form.value.itemname;
        value.otherdescription= form.value.otherdescription;
        value.otp= form.value.otp;
        value.quantity= form.value.quantity;
        value.remark= form.value.remark;
        value.remarks= form.value.remarks;
        value.specdrawing= form.value.specdrawing;
        value.status= form.value.status;
        value.totalprice= form.value.totalprice;

        value.rejectreason= form.value.rejectreason;
        value.safestock= form.value.safestock;
        value.rol= form.value.rol;
        value.livestock= form.value.livestock;
        value.minstock= form.value.minstock;

      }
      this.empdatass = {

        "id" : form.value.id,
        "acknowledge" : form.value.acknowledge,
        "channel" : form.value.channel,
        "date" : form.value.date,
        "empname" : form.value.empname,
        "issuedby" : form.value.issuedby,
        "itemcode" : form.value.itemcode,
        "itemname" : form.value.itemname,
        "otherdescription" : form.value.otherdescription,
        "otp" : form.value.otp,
        "quantity" : form.value.quantity,
        "remark" : form.value.remark,
        "remarks" : form.value.remarks,
        "specdrawing" : form.value.specdrawing,
        "status" : form.value.status,
        "totalprice" : form.value.totalprice,
        "rejectreason" :form.value.rejectreason,
        "safestock" : form.value.safestock,
       "rol" : form.value.rol,
       "livestock" :form.value.livestock,
        "minstock" : form.value.minstock,

      }
      console.log('values are printing ');

      this.closeEditModal.nativeElement.click();
      return true;
    });
    this.http.put(this.mainurl + 'emprequestakg/'+form.value.id, this.empdatass).subscribe();

    this.showNotification(
      'bg-black',
      'Edit Record Successfully',
      'bottom',
      'right'
    );
  }

  filterDatatable(event) {
    // get the value of the key pressed and make it lowercase
    const val = event.target.value.toLowerCase();
    // get the amount of columns in the table
    const colsAmt = this.columns.length;
    // get the key names of each column in the dataset
    const keys = Object.keys(this.filteredData[0]);
    // assign filtered matches to the active datatable
    this.data = this.filteredData.filter(function(item) {
      // iterate through each row's column data
      for (let i = 0; i < colsAmt; i++) {
        // check for a match
        if (
          item[keys[i]]
            .toString()
            .toLowerCase()
            .indexOf(val) !== -1 ||
          !val
        ) {
          // found match, return true to add to result set
          return true;
        }
      }
    });
    // whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }
  getId(min, max) {
    // min and max included
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
  openSnackBar(message: string) {
    this._snackBar.open(message, '', {
      duration: 2000,
      verticalPosition: 'bottom',
      horizontalPosition: 'right',
      panelClass: ['bg-red']
    });
  }
  showNotification(colorName, text, placementFrom, placementAlign) {
    this._snackBar.open(text, '', {
      duration: 2000,
      verticalPosition: placementFrom,
      horizontalPosition: placementAlign,
      panelClass: colorName
    });
  }
}
