import { HttpClient } from '@angular/common/http';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatatableComponent } from '@swimlane/ngx-datatable';

@Component({
  selector: 'app-safetable',
  templateUrl: './safetable.component.html',
  styleUrls: ['./safetable.component.sass']
})
export class SafetableComponent implements OnInit {
  @ViewChild('roleTemplate', { static: true }) roleTemplate: TemplateRef<any>;
  @ViewChild(DatatableComponent, { static: false }) table: DatatableComponent;
  @ViewChild('closeAddModal', { static: false }) closeAddModal;
  @ViewChild('closeEditModal', { static: false }) closeEditModal;
  rows = [];
  selectedRowData: selectRowInterface;
  // newUserImg = 'assets/images/user/user1.jpg';
  data = [];
  filteredData = [];
  editForm: FormGroup;
  register: FormGroup;
  selectedOption: string;
  columns = [
    { name: 'id' },
    { name: 'ItemName' },
    { name: 'category' },
    { name: 'purchasecost' },
    { name: 'minstock' },
    {name: 'safestock'},
    { name: 'safestockpercent' },


  ];
  // Table 2
  tb2columns = [
    { name: 'id' },
    { name: 'ItemName' },
    { name: 'category' },
    { name: 'purchasecost' },
    { name: 'minstock' },
    { name: 'safestock' },
    { name: 'safestockpercent' },


  ];
  tb2data = [];
  tb2filteredData = [];
  @ViewChild(DatatableComponent, { static: false }) table2: DatatableComponent;
  type: any;
  name: any;
  code: any;
  constructor(private fb: FormBuilder, private _snackBar: MatSnackBar , private http: HttpClient ) {
    this.editForm = this.fb.group({
      id: new FormControl(),
      category: new FormControl(),
      ItemName: new FormControl(),
      purchasecost: new FormControl(),
      minstock: new FormControl(),
      safestock: new FormControl(),
      safestockpercent: new FormControl(),
    });
  }
  mainurl
  ngOnInit() {
     this.mainurl = localStorage.getItem('mainurl');

    this.fetch(data => {
      this.data = data;
      this.filteredData = data;
    });
    // Table 2
    this.tb2fetch(data => {
      this.tb2data = data;
      this.tb2filteredData = data;
    });
    this.register = this.fb.group({
      //
      ItemName: [''],
      id: [''],
      category: [''],
      purchasecost: [''],
      safestockpercent: [''],
      safestock: [''],
      minstock: ['']
    });
  }
  fetch(cb) {

    const req = new XMLHttpRequest();
    req.open('GET', this.mainurl + 'productsafestockper');
    req.onload = () => {
      const data = JSON.parse(req.response);
      cb(data);
    };



    req.send();
  }
  // Table 2
  tb2fetch(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'assets/data/ngx-data.json');
    req.onload = () => {
      const data = JSON.parse(req.response);
      cb(data);
    };
    req.send();
  }
  // Table 2
  tb2filterDatatable(event) {
    // get the value of the key pressed and make it lowercase
    const val = event.target.value.toLowerCase();
    // get the amount of columns in the table
    const colsAmt = this.tb2columns.length;
    // get the key names of each column in the dataset
    const keys = Object.keys(this.tb2filteredData[0]);
    // assign filtered matches to the active datatable
    this.tb2data = this.tb2filteredData.filter(function(item) {
      // iterate through each row's column data
      for (let i = 0; i < colsAmt; i++) {
        // check for a match
        if (
          item[keys[i]]
            .toString()
            .toLowerCase()
            .indexOf(val) !== -1 ||
          !val
        ) {
          // found match, return true to add to result set
          return true;
        }
      }
    });
    // whenever the filter changes, always go back to the first page
    this.table2.offset = 0;
  }
  editRow(row, rowIndex) {
    this.editForm.setValue({
      id: row.id,
      name: row.name,
      code: row.code,

    });
    this.selectedRowData = row;
  }
  addRow() {
    this.register.patchValue({
      // id: this.getId(10, 100),
      name: this.name,
      code: this.code
    });
  }
  deleteRow(row) {
    // this.data = this.arrayRemove(this.data, row.id);
    // this.showNotification(
    //   'bg-red',
    //   'Delete Record Successfully',
    //   'bottom',
    //   'right'
    // );
    this.data = this.arrayRemove(this.data, row.id);
    this.http.delete(this.mainurl + 'productsafestock/'+row.id).subscribe();
    this.showNotification(
      'bg-red',
      'Delete Record Successfully',
      'bottom',
      'right'
    );
  }
  arrayRemove(array, id) {
    return array.filter(function(element) {
      return element.id != id;
    });
  }
  onEditSave(form: FormGroup) {
    // this.data = this.data.filter((value, key) => {
    //   if (value.id == form.value.id) {
    //     value.type = form.value.type;

    //   }
    //   this.closeEditModal.nativeElement.click();
    //   return true;
    // });
    this.data = this.data.filter((value, key) => {
      if (value.id == form.value.id) {
        value.code = form.value.code;
        value.name = form.value.name;
      }
      let empdata = {
        'code' : form.value.code,
        'name' : form.value.name,



      }
      this.http.put(this.mainurl + 'productsafestock/'+form.value.id, empdata).subscribe();

      this.closeEditModal.nativeElement.click();
      return true;
    });
    this.showNotification(
      'bg-black',
      'Edit Record Successfully',
      'bottom',
      'right'
    );
  }
  onAddRowSave(form: FormGroup) {
    this.data.push(form.value);
    this.http.post(this.mainurl + 'productsafestock', form.value).subscribe();
    this.data = [...this.data];
    // console.log(this.data);
    form.reset();
    this.closeAddModal.nativeElement.click();
    this.showNotification(
      'bg-green',
      'Add Record Successfully',
      'bottom',
      'right'
    );
  }
  filterDatatable(event) {
    // get the value of the key pressed and make it lowercase
    const val = event.target.value.toLowerCase();
    // get the amount of columns in the table
    const colsAmt = this.columns.length;
    // get the key names of each column in the dataset
    const keys = Object.keys(this.filteredData[0]);
    // assign filtered matches to the active datatable
    this.data = this.filteredData.filter(function(item) {
      // iterate through each row's column data
      for (let i = 0; i < colsAmt; i++) {
        // check for a match
        if (
          item[keys[i]]
            .toString()
            .toLowerCase()
            .indexOf(val) !== -1 ||
          !val
        ) {
          // found match, return true to add to result set
          return true;
        }
      }
    });
    // whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }
  getId(min, max) {
    // min and max included
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
  openSnackBar(message: string) {
    this._snackBar.open(message, '', {
      duration: 2000,
      verticalPosition: 'bottom',
      horizontalPosition: 'right',
      panelClass: ['bg-red']
    });
  }
  showNotification(colorName, text, placementFrom, placementAlign) {
    this._snackBar.open(text, '', {
      duration: 2000,
      verticalPosition: placementFrom,
      horizontalPosition: placementAlign,
      panelClass: colorName
    });
  }
}
export interface selectRowInterface {
  id: String;
  name: String;
  code: String;
}
