import { HttpClient } from '@angular/common/http';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatatableComponent } from '@swimlane/ngx-datatable';

@Component({
  selector: 'app-findmatireals',
  templateUrl: './findmatireals.component.html',
  styleUrls: ['./findmatireals.component.sass']
})
export class FindmatirealsComponent implements OnInit {
  @ViewChild('roleTemplate', { static: true }) roleTemplate: TemplateRef<any>;
  @ViewChild(DatatableComponent, { static: false }) table: DatatableComponent;
  @ViewChild('closeAddModal', { static: false }) closeAddModal;
  @ViewChild('closeEditModal', { static: false }) closeEditModal;
  rows = [];
  selectedRowData: selectRowInterface;
  // newUserImg = 'assets/images/user/user1.jpg';
  data = [];
  filteredData = [];
  editForm: FormGroup;
  register: FormGroup;
  selectedOption: string;
  columns = [
    { name: 'productid' },
    { name: 'productcode' },
    { name: 'name' },
    { name: 'description' },
    { name: 'description1' },
    { name: 'description2' },
    { name: 'category' },
    { name: 'totalorders' }  ,
    { name: 'minstock' },
    { name: 'totalcostordered' },
    { name: 'purchasecost' },
    { name: 'rack' },
    { name: 'units' }

  ];
  // Table 2
  tb2columns = [
    { name: 'productid' },
    { name: 'productcode' },
    { name: 'name' },
    { name: 'description' },
    { name: 'description1' },
    { name: 'description2' },
    { name: 'category' },
    { name: 'totalorders' }  ,
    { name: 'minstock' },
    { name: 'totalcostordered' },
    { name: 'purchasecost' },
    { name: 'rack' },
    { name: 'units' }

  ];
  tb2data = [];
  tb2filteredData = [];
  @ViewChild(DatatableComponent, { static: false }) table2: DatatableComponent;
  type: any;
  constructor(private fb: FormBuilder, private _snackBar: MatSnackBar , private http: HttpClient ) {
    this.editForm = this.fb.group({

      productid: new FormControl(),
      productcode: new FormControl(),
      description: new FormControl(),
      name: new FormControl(),
      category: new FormControl(),
      description1: new FormControl(),
      description2: new FormControl(),
      totalorders: new FormControl(),
      minstock:new FormControl(),
      totalcostordered: new FormControl(),
      purchasecost: new FormControl(),
      rack: new FormControl(),
      units: new FormControl(),

    });
  }
  mainurl;
  ngOnInit() {
    this.mainurl = localStorage.getItem('mainurl');
    this.fetch(data => {
      this.data = data;
      this.filteredData = data;
    });
    // Table 2
    this.tb2fetch(data => {
      this.tb2data = data;
      this.tb2filteredData = data;
    });
    this.register = this.fb.group({
      //
      productid: [''],
      productcode: [''],
      description: [''],
      name: [''],
      category: [''],
      description1: [''],
      description2: [''],
      totalorders: [''],
      minstock:new FormControl(),
      totalcostordered: [''],
      purchasecost: [''],
      rack: [''],
      units: [''],

    });
  }
  fetch(cb) {

    const req = new XMLHttpRequest();
    req.open('GET', this.mainurl + 'product');
    req.onload = () => {
      const data = JSON.parse(req.response);
      cb(data);
      console.log('products', data);

    };



    req.send();
  }
  // Table 2
  tb2fetch(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'assets/data/ngx-data.json');
    req.onload = () => {
      const data = JSON.parse(req.response);
      cb(data);
    };
    req.send();
  }
  // Table 2
  tb2filterDatatable(event) {
    // get the value of the key pressed and make it lowercase
    const val = event.target.value.toLowerCase();
    // get the amount of columns in the table
    const colsAmt = this.tb2columns.length;
    // get the key names of each column in the dataset
    const keys = Object.keys(this.tb2filteredData[0]);
    // assign filtered matches to the active datatable
    this.tb2data = this.tb2filteredData.filter(function(item) {
      // iterate through each row's column data
      for (let i = 0; i < colsAmt; i++) {
        // check for a match
        if (
          item[keys[i]]
            .toString()
            .toLowerCase()
            .indexOf(val) !== -1 ||
          !val
        ) {
          // found match, return true to add to result set
          return true;
        }
      }
    });
    // whenever the filter changes, always go back to the first page
    this.table2.offset = 0;
  }
  editRow(row, rowIndex) {
    this.editForm.setValue({


      productid:row.productid,
      productcode:row.productcode,
      description:row.description,
      name:row.name,
      category:row.category,
      description1:row.description1,
      description2:row.description2,
      totalorders:row.totalorders,
      minstock: row.minstock,
      totalcostordered:row.totalcostordered,
      purchasecost:row.purchasecost,
      rack:row.rack,
      units:row.units,

    });
    this.selectedRowData = row;
  }
  addRow() {
    this.register.patchValue({
      // id: this.getId(10, 100),
      type: this.type
    });
  }
  deleteRow(row) {
    // this.data = this.arrayRemove(this.data, row.id);
    // this.showNotification(
    //   'bg-red',
    //   'Delete Record Successfully',
    //   'bottom',
    //   'right'
    // );
    this.data = this.arrayRemove(this.data, row.id);
    this.http.delete(this.mainurl + 'product/'+row.id).subscribe();
    this.showNotification(
      'bg-red',
      'Delete Record Successfully',
      'bottom',
      'right'
    );
  }
  arrayRemove(array, id) {
    return array.filter(function(element) {
      return element.id != id;
    });
  }
  onEditSave(form: FormGroup) {
    // this.data = this.data.filter((value, key) => {
    //   if (value.id == form.value.id) {
    //     value.type = form.value.type;

    //   }
    //   this.closeEditModal.nativeElement.click();
    //   return true;
    // });
    this.data = this.data.filter((value, key) => {
      if (value.productid == form.value.productid) {



        value.name = form.value.name;
        value.category = form.value.category;
        value.productcode = form.value.productcode;
        value.description = form.value.description;
        value.description1 = form.value.description1;
        value.description2 = form.value.description2;
        value.totalorders = form.value.totalorders;
        value.minstock = form.value.minstock;
        value.totalcostordered = form.value.totalcostordered;
        value.purchasecost = form.value.purchasecost;
        value.rack = form.value.rack;
        value.units = form.value.units;



      }
      let empdata = {



        "productcode" :form.value.productcode,
        "description" :form.value.description,
        "name" :form.value.name,
        "category" :form.value.category,
        "description1" :form.value.description1,
        "description2" :form.value.description2,
        "totalorders" :form.value.totalorders,
        "minstock" :form.value.minstock,
        "totalcostordered" :form.value.totalcostordered,
        "purchasecost" :form.value.purchasecost,
        "rack" :form.value.rack,
        "units" :form.value.units


      }


      console.log(empdata);

      this.http.put(this.mainurl + 'product/'+form.value.productid, empdata).subscribe();

      this.closeEditModal.nativeElement.click();
      return true;
    });
    this.showNotification(
      'bg-black',
      'Edit Record Successfully',
      'bottom',
      'right'
    );
  }
  onAddRowSave(form: FormGroup) {
    this.data.push(form.value);
    this.http.post(this.mainurl + 'product', form.value).subscribe();
    this.data = [...this.data];
    // console.log(this.data);
    form.reset();
    this.closeAddModal.nativeElement.click();
    this.showNotification(
      'bg-green',
      'Add Record Successfully',
      'bottom',
      'right'
    );
  }
  filterDatatable(event) {
    // get the value of the key pressed and make it lowercase
    const val = event.target.value.toLowerCase();
    // get the amount of columns in the table
    const colsAmt = this.columns.length;
    // get the key names of each column in the dataset
    const keys = Object.keys(this.filteredData[0]);
    // assign filtered matches to the active datatable
    this.data = this.filteredData.filter(function(item) {
      // iterate through each row's column data
      for (let i = 0; i < colsAmt; i++) {
        // check for a match
        if (
          item[keys[i]]
            .toString()
            .toLowerCase()
            .indexOf(val) !== -1 ||
          !val
        ) {
          // found match, return true to add to result set
          return true;
        }
      }
    });
    // whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }
  getId(min, max) {
    // min and max included
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
  openSnackBar(message: string) {
    this._snackBar.open(message, '', {
      duration: 2000,
      verticalPosition: 'bottom',
      horizontalPosition: 'right',
      panelClass: ['bg-red']
    });
  }
  showNotification(colorName, text, placementFrom, placementAlign) {
    this._snackBar.open(text, '', {
      duration: 2000,
      verticalPosition: placementFrom,
      horizontalPosition: placementAlign,
      panelClass: colorName
    });
  }
}
export interface selectRowInterface {
  productid:String;
  productcode:String;
  description:String;
  name:String;
  category:String;
  description1:String;
  description2:String;
  totalorders:String;
  minstock:String;
  purchasecost:String;
  rack:String;
  units:String;

}
