import { ItemtsnComponent } from './itemtsn/itemtsn.component';
import { StockupdateComponent } from './stockupdate/stockupdate.component';
import { ReportsComponent } from './reports/reports.component';
import { OthersreqComponent } from './othersreq/othersreq.component';
import { CITRIXComponent } from './citrix/citrix.component';
import { RequestdeatialsComponent } from './requestdeatials/requestdeatials.component';
import { StoremngComponent } from './storemng/storemng.component';
import { StoreboardComponent } from './storeboard/storeboard.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FindmatirealsComponent } from './findmatireals/findmatireals.component';
import { RequestakgComponent } from './requestakg/requestakg.component';
import { RoltableComponent } from './roltable/roltable.component';
import { LivetableComponent } from './livetable/livetable.component';
import { SafetableComponent } from './safetable/safetable.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'main',
    pathMatch: 'full'
  },
  {
    path: 'main',
    component: StoreboardComponent
  },
  {
    path: 'reqdata',
    component: RequestdeatialsComponent
  },
  {
    path: 'safet',
    component: SafetableComponent
  },
  {
    path: 'itemtrn',
    component: ItemtsnComponent
  },
  {
    path: 'livet',
    component: LivetableComponent
  },
  {
    path: 'rolt',
    component: RoltableComponent
  },
  {
    path: 'strmng',
    component: StoremngComponent
  },
  {
    path: 'reports',
    component: ReportsComponent
  },
  {
    path: 'stockupdate',
    component: StockupdateComponent
  },
  {
    path: 'otherreq',
    component: OthersreqComponent
  },
  {
    path: 'citrix',
    component: CITRIXComponent
  },
  {
    path: 'rqtack',
    component: RequestakgComponent
  },
  {
    path: 'findmatirial',
    component: FindmatirealsComponent
  },
  // {
  //   path: 'dashboard3',
  //   component: Dashboard3Component
  // }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class storemngRoutingModule {}
